import React, {useState} from 'react';
import s from './BurgerNav.module.scss'
import {Link} from 'react-scroll'
import burger from '../../assets/img/burger.svg'
import burgerClose from '../../assets/img/bgc.svg'


export const BurgerNav = () => {
    const [burgerIsActive, setBurgerIsActive] = useState(false)
    const changeBurgerHandler = () => {
        setBurgerIsActive(prevState => !prevState)
    }
    return (
        <div className={s.navBlock}>
            {burgerIsActive && <div className={`${s.burgerItems} ${s.show}`}>
                <Link activeClass={s.active}
                      to="main"
                      spy={true}
                      smooth={true}
                      hashSpy={true}
                      offset={-65}
                      duration={1000}
                      delay={300}
                      isDynamic={true}
                      ignoreCancelEvents={false}
                      spyThrottle={500}>
                    Main
                </Link>
                <Link activeClass={s.active}
                      to="skills"
                      spy={true}
                      smooth={true}
                      hashSpy={true}
                      offset={-60}
                      duration={1000}
                      delay={300}
                      isDynamic={true}
                      ignoreCancelEvents={false}
                      spyThrottle={500}>
                    Skills
                </Link>
                <Link activeClass={s.active}
                      to="projects"
                      spy={true}
                      smooth={true}
                      hashSpy={true}
                      offset={-60}
                      duration={1000}
                      delay={300}
                      isDynamic={true}
                      ignoreCancelEvents={false}
                      spyThrottle={500}>
                    Projects
                </Link>
                <Link activeClass={s.active}
                      to="contacts"
                      spy={true}
                      smooth={true}
                      hashSpy={true}
                      offset={-60}
                      duration={1000}
                      delay={300}
                      isDynamic={true}
                      ignoreCancelEvents={false}
                      spyThrottle={500}>
                    Contacts
                </Link>
            </div>}
            <div>
                {burgerIsActive ?
                    <img
                        onClick={changeBurgerHandler}
                        className={s.burgerBtn}
                        src={burgerClose}
                        alt={'close burger menu'}/>
                    : <img
                        onClick={changeBurgerHandler}
                        className={s.burgerBtn}
                        src={burger}
                        alt={'open burger menu'}/>}
            </div>
        </div>
    );
};

